import React from 'react'
import { connect } from 'react-redux'
import {withRouter} from 'react-router-dom'

import s from './Body.module.css'
import { addMessage, setMessages, delMessages, getMessagesThunk, postMessageThunk } from './chatReducer'

import MessageForm1 from './Message/messageForm'

import Users from './Users/Users'
import WindowChat from './windowChat/windowChat'
import axios from 'axios'
import { compose } from 'redux'


class Body extends React.Component{
    
     componentDidMount(){
        
            this.props.getMessagesThunk()
               
    }
    
   



    message(text){
        
        this.props.postMessageThunk(text.messageText, this.props.user)
        text.messageText=''
        
    }

    delMessage(id){
        axios.delete('https://chatmessages.herokuapp.com/api/messages'+id,{data: {id:''}})
        
    }
    
    

    render(){
        
    return(
        
        <div className={s.body}>
            <div className={s.chat}>
                <WindowChat messageName={this.props.message} delMess={this.delMessage.bind(this)}/>
            </div>
            <div className={s.people}>
                <Users />
            </div>
            <div className={s.mess}>
               <MessageForm1 onSubmit={this.message.bind(this)} />
                
            </div>
        </div>

    )}
}





const mapStateToProps=(state)=>({
    
    message: state.MessageReducer.message,
    user: state.reducerStateName.name
})




export const BodyContainer = compose(withRouter, connect(mapStateToProps, {addMessage, setMessages, delMessages, getMessagesThunk, postMessageThunk}))(Body)

