import React from 'react'
import s from './windowChat.module.css'
import $ from 'jquery'
import { NavLink, withRouter } from 'react-router-dom';
import { Preloader } from '../utils/Preloader';
import {decode,encode} from 'js-base64'

class WindowChat extends React.PureComponent{
    
    click (e) {
        this.props.delMess(this.props.location.pathname)
        // console.log(this.props.location.pathname)
    }

    
    
    render(){
        
        let preloader =false
        if(this.props.messageName.length<1){
            preloader=true
        }
    return(
        <div id='chat'  >
         {preloader &&  <Preloader />}
        <div  className={s.chat} >
            
            {this.props.messageName.map(u=><NavLink to={"/"+u._id} className={s.navlink}><div key={u.id}><span>{decode(u.name) + ': '}</span><span>{decode(u.message)}</span>
            
            <div className={s.del} onClick={this.click.bind(this)} >x</div>
            </div></NavLink>)
            
            }
            
            
        </div>
        </div>
    )}
}



export default withRouter( WindowChat)