import React from 'react'
import { connect } from 'react-redux'
import s from './Header.module.css'
import HeaderFormRedux from './HeaderForm'
import {addUsers, setUsers, getUsersThunk, setUsersThunk,checkIsAuth} from './reducerHeader'
import {NavLink, Route, withRouter} from 'react-router-dom'
import HeaderFormReduxSigin from './HeaderFormSigin'
import {Name} from './../Header/reducerStateName'
import { compose } from 'redux'
import Axios from 'axios'
import {decode, encode} from 'js-base64'





class Header extends React.PureComponent{

    state = {
        login: ''
    }

    componentDidMount(){

        this.props.getUsersThunk()
        
        // setInterval(()=>{this.props.getUsersThunk()}, 3000)
        
    }

    submitRegistr (text) {
        
        // let text64 = decode(text)
        // отправка на сервер 
        this.props.setUsersThunk(text)


    }
    componentWillMount(){
        // this.timer=null
        
    }
    
    submitSigin (text) {
        let id = this.props.users.map(user=>user._id)
        let login = this.props.users.map(user=>user.login)
        let pass = this.props.users.map(user=>user.pass)
        
        for(let i=0;i<login.length;i++){
            if(encode(text.login)===login[i] && encode(text.pass) === pass[i]){
                this.setState({login: text.login})
                this.props.Name(text.login)

                

                let isAuth = {isAuth: true}
                // Axios.put('https://chatmessages.herokuapp.com/api/users'+id[i],isAuth)
                
            }
        }
        
    }
    Exit =()=>{
        this.setState({login: ''})
        let id = this.props.users.map(user=>user._id)
        
        // for(let i=0;i<id.length;i++){
            
                
                
                // console.log(id[i])
                // Axios.put('https://chatmessages.herokuapp.com/api/users'+id[i],{isAuth: false})
                
            
        // }
    }


    render(){
        
       
        
    return (
        <div className={s.header}>
            <h1>Welcom to chat {this.state.login}</h1>
            {!this.state.login && 
            <div>
            <NavLink to={'/reg'} className={s.link}>Register</NavLink>
            <span> </span>
            
            <NavLink to={'/sigin'} className={s.link}>Sigin</NavLink>
            <Route exact path='/reg' render={()=><HeaderFormRedux onSubmit={this.submitRegistr.bind(this)}/>} />
            <Route exact path='/sigin' render={()=><HeaderFormReduxSigin onSubmit={this.submitSigin.bind(this)}/>} />
            </div>
    }
    {this.state.login && 
        <div>
            <button onClick={this.Exit}>Exit</button>
        </div>
    }
        </div>
    )}
}



const mapStateToProps = (state) =>({
    users: state.reducerHeader.users,
    isAuth: state.reducerHeader.isAuth
})

const HeaderContainer =compose(withRouter, connect (mapStateToProps, {addUsers, Name, setUsers, getUsersThunk, setUsersThunk,checkIsAuth})) (Header)


export default HeaderContainer