export const postMessageThunk=(text, user)=>{
    
    return dispatch=>{
        let message = {name: encode(user), message: encode(text)}
        
       
        axios.post('https://chatmessages.herokuapp.com/api/messages', message)
        
        dispatch (addMessage(text, user))
        
        text= ''
    }
}